import React, { Component } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { compose } from "redux";
import actions from "../../../../redux/financial/interbankTransfer/actions";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";

import {
  Modal,
  Tooltip,
  Table,
  Collapse,
  Select,
  DatePicker,
  Input,
  Row,
  Col,
  Empty
} from "antd";
import {
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;
const Search = Input.Search;
const { getImportInterbankTransfers } = actions;

class ModalInterbankTransfer extends Component {
  state = {
    filter: {
      withdraw_id_cash_bank: null,
      withdraw_id_location: null,
      deposit_id_cash_bank: null,
      deposit_id_location: null,
      status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  handleSelect = value => {
    const { handleSelectModal } = this.props;
    handleSelectModal(value);
  };

  handleLocationChange = id_location => {
    const { getImportInterbankTransfers } = this.props;
    const string = "id_location=" + id_location;
    getImportInterbankTransfers(1, null, string);
  };

  componentDidUpdate(prevProps) {
    const { id_location } = this.props;

    if (prevProps.id_location !== id_location) {
      this.handleLocationChange(id_location);
      this.setState({
        filter: {
          ...this.state.filter,
          id_location: id_location
        }
      });
    }
  }

  // Handle search
  handleSearch = value => {
    const { getImportInterbankTransfers } = this.props;
    getImportInterbankTransfers(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getImportInterbankTransfers } = this.props;
    this.setState({
      filter: {
        withdraw_id_cash_bank: null,
        withdraw_id_location: null,
        deposit_id_cash_bank: null,
        deposit_id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportInterbankTransfers();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } 
    else if(name === "withdraw_id_location"){
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          withdraw_id_cash_bank: null
        }
      });
    }
    else if(name === "deposit_id_location"){
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          deposit_id_cash_bank: null
        }
      });
    }
    else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getImportInterbankTransfers } = this.props;
    //const { current_page } = this.props.interbankTransfer;
    const { filter } = this.state;
    let string = "";

    if (filter.withdraw_id_location) {
      string += "withdraw_id_location=" + filter.withdraw_id_location;
    }
    if (filter.withdraw_id_cash_bank) {
      string
        ? (string += "&withdraw_id_cash_bank=" + filter.withdraw_id_cash_bank)
        : (string += "withdraw_id_cash_bank=" + filter.withdraw_id_cash_bank);
    }
    if (filter.deposit_id_location) {
      string
        ? (string += "&deposit_id_location=" + filter.deposit_id_location)
        : (string += "deposit_id_location=" + filter.deposit_id_location);
    }
    if (filter.deposit_id_cash_bank) {
      string
        ? (string += "&deposit_id_cash_bank=" + filter.deposit_id_cash_bank)
        : (string += "deposit_id_cash_bank=" + filter.deposit_id_cash_bank);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getImportInterbankTransfers(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getImportInterbankTransfers } = this.props;
    this.setState({
      filter: {
        withdraw_id_cash_bank: null,
        withdraw_id_location: null,
        deposit_id_cash_bank: null,
        deposit_id_location: null,
        status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getImportInterbankTransfers();
  };

  // Handle pagination
  handlePageChange = page => {
    const { getImportInterbankTransfers } = this.props;
    const { import_search, import_filter } = this.props.interbankTransfer;
    getImportInterbankTransfers(page, import_search, import_filter);
  };

  

  render() {

    let columns = [
      {
        title: getLanguage({id: "interbankTransfers.no"}),
        dataIndex: "interbank_transfer_no",
        rowKey: "interbank_transfer_no",
        width: "25%",
        render: (text, row) => {
          let className;
          switch (row.status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "ordered";
              break;
            // case 2:
            //   className = "closed";
            //   break;
            case 3:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }
  
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <Tooltip
                placement="bottom"
                title={<IntlMessages id="tooltip.select" />}
              >
                <a
                  onClick={e => {
                    e.preventDefault();
                    this.handleSelect(row.id_interbank_transfer);
                  }}
                  href="javascript:;"
                >
                  {text ? <TransNo>{text}</TransNo> : <TransNo>{getLanguage({id: "new"})}</TransNo>}
                </a>
              </Tooltip>
            </div>
          );
        }
      },
      {
        title: getLanguage({id: "date"}),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "15%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({id: "interbankTransfers.withdrawLocation"}),
        dataIndex: "withdraw_location.location_name",
        rowKey: "withdraw_location.location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "interbankTransfers.withdrawCashBank"}),
        dataIndex: "withdraw_cash_bank.cash_bank_code",
        rowKey: "withdraw_cash_bank.cash_bank_code",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "interbankTransfers.depositLocation"}),
        dataIndex: "deposit_location.location_name",
        rowKey: "deposit_location.location_name",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "interbankTransfers.depositCashBank"}),
        dataIndex: "deposit_cash_bank.cash_bank_code",
        rowKey: "deposit_cash_bank.cash_bank_code",
        width: "20%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "createdBy"}),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "25%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "status"}),
        dataIndex: "status",
        rowKey: "status",
        width: "15%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({id: "open"});
              break;
            case 2:
              className = "ordered";
              display = getLanguage({id: "processed"});
              break;
            // case 2:
            //   className = "closed";
            //   display = "Processed";
            //   break;
            case 3:
              className = "cancelled";
              display = getLanguage({id: "cancelled"});
              break;
            default:
              className = "new";
              display = getLanguage({id: "open"});
              break;
          }
  
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const { visible, handleModal, interbankTransfer, id_location } = this.props;
    const {
      import_interbank_transfers,
      withdraw_locations,
      deposit_locations,
      cash_banks
    } = interbankTransfer ? interbankTransfer : [];

    const { loadingImport } = interbankTransfer ? interbankTransfer : false;
    const dataSource = [];
    Object.keys(import_interbank_transfers).map((interbank_transfer, index) => {
      return dataSource.push({
        ...import_interbank_transfers[interbank_transfer],
        key: interbank_transfer
      });
    });

    const {
      import_current_page,
      import_page_size,
      import_total
    } = this.props.interbankTransfer;

    return (
      <Modal
        title={<IntlMessages id="interbankTransfers.copy" />}
        visible={visible}
        centered
        footer={null}
        keyboard={true}
        onCancel={handleModal}
        width="95%"
      >
        <Row style={{ marginBottom: "10px" }} type="flex">
          <Col span={18}>
            <Search
              placeholder={
                getLanguage({id: "interbankTransfers.no"}) + ", " +
                getLanguage({id: "createdBy"})
              }              
              onSearch={this.handleSearch}
              style={{ width: "99%", height: "36px" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
          </Col>
          <Col span={6}>
            <Button
              type="primary"
              onClick={this.handleSearch.bind(this, this.state.search)}
              style={{ marginRight: "2%" }}
            >
              <IntlMessages id="button.search" />
            </Button>

            <Button onClick={this.handleResetSearch}>
              <IntlMessages id="filter.reset" />
            </Button>
          </Col>
        </Row>
        <Row style={{ marginBottom: "10px" }}>
          <Col>
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="interbankTransfers.withdrawLocation" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "withdraw_id_location"
                      )}
                      value={
                        this.state.filter.withdraw_id_location
                          ? this.state.filter.withdraw_id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="interbankTransfers.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {withdraw_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="interbankTransfers.depositLocation" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "deposit_id_location"
                      )}
                      value={
                        this.state.filter.deposit_id_location
                          ? this.state.filter.deposit_id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="interbankTransfers.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {deposit_locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="interbankTransfers.withdrawCashBank" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "withdraw_id_cash_bank"
                      )}
                      value={
                        this.state.filter.withdraw_id_cash_bank
                          ? this.state.filter.withdraw_id_cash_bank
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="interbankTransfers.filter.select.cashBank" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.withdraw_id_location ? (
                          <Empty
                            description={<IntlMessages id="noData" />}
                          />
                        ) : (
                            <Empty
                              description={<IntlMessages id="interbankTransfers.withdrawLocation.select" />}
                            />
                          )
                      }
                    >
                      {cash_banks.map((data, key) => {
                        if (this.state.filter.withdraw_id_location === data.id_location && this.state.filter.deposit_id_cash_bank !== data.id_cash_bank) {
                          return (
                            <Option key={key} value={data.id_cash_bank}>
                              {data.bank_account_number === null
                                ? data.cash_bank_code
                                : data.cash_bank_code +
                                " (" +
                                data.bank_account_number +
                                ")"}
                            </Option>
                          )
                        }
                      })}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="interbankTransfers.depositCashBank" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "deposit_id_cash_bank"
                      )}
                      value={
                        this.state.filter.deposit_id_cash_bank
                          ? this.state.filter.deposit_id_cash_bank
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="interbankTransfers.filter.select.cashBank" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingImport}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.deposit_id_location ? (
                          <Empty
                            description={<IntlMessages id="noData" />}
                          />
                        ) : (
                            <Empty
                              description={<IntlMessages id="interbankTransfers.depositLocation.select" />}
                            />
                          )
                      }
                    >
                      {cash_banks.map((data, key) => {
                        if (this.state.filter.deposit_id_location === data.id_location && this.state.filter.withdraw_id_cash_bank !== data.id_cash_bank) {
                          return (
                            <Option key={key} value={data.id_cash_bank}>
                              {data.bank_account_number === null
                                ? data.cash_bank_code
                                : data.cash_bank_code +
                                " (" +
                                data.bank_account_number +
                                ")"}
                            </Option>
                          )
                        }
                      })}
                    </Select>
                  </Col>
                </Row>
                <Row
                  type="flex"
                  justify="start"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="interbankTransfers.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <Col span={8}>
                      <label>Status</label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(this, "status")}
                        value={
                          this.state.filter.status ? this.state.filter.status : []
                        }
                        style={{ width: "90%" }}
                        placeholder="Status"
                        optionFilterProp="children"
                        filterOption={true}
                        // loading={this.props.location.loadingFilterSites}
                        allowClear={true}
                        //showArrow={false}
                      >
                        {/* <Option value={4}>New</Option> */}
                        <Option value={1}>{getLanguage({id: "open"})}</Option>
                        <Option value={2}>{getLanguage({id: "processed"})}</Option>
                        <Option value={3}>{getLanguage({id: "cancelled"})}</Option>
                      </Select>
                    </Col>
                    <Col span={8}>
                      <label>
                        <IntlMessages id="dataPerPage" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "pagination"
                        )}
                        value={
                          this.state.filter.pagination
                            ? this.state.filter.pagination
                            : import_page_size
                        }
                        style={{ width: "80%" }}
                        // placeholder={
                        //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                        // }
                        optionFilterProp="children"
                        filterOption={true}
                        //allowClear={true}
                        //showArrow={false}
                      >
                        <Option key={1} value={10}>
                          10
                        </Option>
                        <Option key={2} value={25}>
                          25
                        </Option>
                        <Option key={3} value={50}>
                          50
                        </Option>
                        <Option key={4} value={100}>
                          100
                        </Option>
                      </Select>
                    </Col>
                    <Col
                      span={8}
                      style={{
                        marginTop: "24px"
                      }}
                    >
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                      >
                        <IntlMessages id="filter.submit" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </Col>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </Col>
        </Row>

        <Row>
          <Col>
            {dataSource.length === 0 && !loadingImport ? (
              <Empty
                description={
                  <IntlMessages id="interbankTransfers.table.noData" />
                }
              />
            ) : (
              <Table
                dataSource={dataSource}
                columns={columns}
                loading={loadingImport}
                // Set pagination, jika ada item_requests_page_size mengunakan pagination API
                // jika tidak ada item_requests_page_size menggunakan pagination bawaan
                pagination={
                  import_page_size
                    ? {
                        current: import_current_page,
                        pageSize: parseInt(import_page_size),
                        total: import_total,
                        onChange: this.handlePageChange.bind(this)
                      }
                    : true
                }
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

export default compose(
  connect(
    null,
    { getImportInterbankTransfers }
  )
)(ModalInterbankTransfer);
