import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import InterbankTransferCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/interbankTransfer/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";


const TabPane = Tabs.TabPane;

const redirectTo = "/dashboard/financials/interbank-transfers"

class CreateInterbankTransfer extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createInterbankTransfer, getImportInterbankTransfers } = this.props;
    createInterbankTransfer();
    getImportInterbankTransfers();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.interbankTransfer.error) {
      notification("error", this.props.interbankTransfer.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.interbankTransfer.success) {
      this.props.history.replace(redirectTo + "/update/" + this.props.interbankTransfer.id_interbank_transfer)
    }
  }

  handleSubmit = value => {
    const { storeInterbankTransfer } = this.props;
    storeInterbankTransfer(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(permissions);
    
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="interbankTransfers.add.title" />}
            style={{ margin:0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="interbankTransfers.tab.detail" />} 
              key="1" 
            >
              <InterbankTransferCreateForm
                submit={this.handleSubmit}
                interbankTransfer={this.props.interbankTransfer}
                permissions={permissions}
                user={user}
              />    
            </TabPane>
            <TabPane tab={<IntlMessages id="interbankTransfers.tab.attachment" />} 
              key="2"
              disabled
            >  
            </TabPane>
          </Tabs>
          
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    interbankTransfer: state.interbankTransfer,
    auth: state.auth
   };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateInterbankTransfer);
