const actions = {
  getInterbankTransfers: (page, search, filter) => ({
    type: 'GET_INTERBANK_TRANSFERS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getImportInterbankTransfers: (page, search, filter) => ({
    type: 'GET_IMPORT_INTERBANK_TRANSFERS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createInterbankTransfer: () => ({
    type: 'CREATE_INTERBANK_TRANSFER_REQUEST'
  }),
  storeInterbankTransfer: data => ({
    type: 'STORE_INTERBANK_TRANSFER_REQUEST',
    payload: { body: data }
  }),
  editInterbankTransfer: id_interbank_transfer => ({
    type: 'EDIT_INTERBANK_TRANSFER_REQUEST',
    payload: { id_interbank_transfer: id_interbank_transfer }
  }),
  updateInterbankTransfer: (id_interbank_transfer, data) => ({
    type: 'UPDATE_INTERBANK_TRANSFER_REQUEST',
    payload: {
      id_interbank_transfer: id_interbank_transfer,
      body: data
    }
  }),
  showInterbankTransfer: id_interbank_transfer => ({
    type: 'SHOW_INTERBANK_TRANSFER_REQUEST',
    payload: { id_interbank_transfer: id_interbank_transfer }
  }),
  processInterbankTransfer: id_interbank_transfer => ({
    type: "PROCESS_INTERBANK_TRANSFER_REQUEST",
    payload: { id_interbank_transfer }
  }),
  cancelInterbankTransfer: (id_interbank_transfer, data) => ({
    type: "CANCEL_INTERBANK_TRANSFER_REQUEST",
    payload: { 
      id_interbank_transfer,
      body : data
    }
  }),
  uploadDocument: (id_interbank_transfer, data) => ({
    type: "UPLOAD_INTERBANK_TRANSFER_DOCUMENT_REQUEST",
    payload: {
      id_interbank_transfer,
      body : data
    }
  }),
  downloadDocument: id_interbank_transfer_document => ({
    type: "DOWNLOAD_INTERBANK_TRANSFER_DOCUMENT_REQUEST",
    payload: {id_interbank_transfer_document}
  }),
  deleteDocument: id_interbank_transfer_document => ({
    type: "DELETE_INTERBANK_TRANSFER_DOCUMENT_REQUEST",
    payload: { id_interbank_transfer_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
